import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      redirect: '/home',
    },
    {
      path: '/home',
      name: 'home',
      component: Home
    },
    {
      path: '/research',
      name: 'research',
      component: () => import('./views/Research.vue')
    },
    {
      path: '/more',
      name: 'more',
      component: () => import('./views/More.vue')
    },
    {
      path: '/publication',
      name: 'publication',
      component: () => import('./views/Publication.vue')
    },
    {
      path: '/awards',
      name: 'awards',
      component: () => import('./views/Awards.vue')
    },
    {
      path: '/services',
      name: 'services',
      component: () => import('./views/Services.vue')
    },
    {
      path: '/teaching',
      name: 'teaching',
      component: () => import('./views/Teaching.vue')
    },
    {
      path: '/students',
      name: 'students',
      component: () => import('./views/Students.vue')
    }
  ]
})
